a:link, a:visited, a:hover, a:active {
  color: white;
  text-decoration: none;
}
.appBlock {
  z-index: 20;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  text-align: center;
  display: none;
  background-color: black;
}
.appContent {
  position: relative;
  display: block;
  margin: 0 auto;
  padding: 0;
  /*aspect-ratio: 16/9;*/
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
.alignMiddle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.layerContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 100%;
  left: 0px;
  z-index: 15;
}

.projectContainer {
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  position: relative;
}
.projectDetail {
  height: auto;
  width: 80%;
  color: black;
  margin: 0 auto;
  padding-bottom: 40vh;
  display: flex;
  flex-direction: column;
}
.projectDetail::before {
  content: '';
  height: 1vh;
  width: 1%;
  margin: 0 auto;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0));
  position: fixed;
  top: 100px;
  left: 50vw;
  z-index: -1;
  animation: ray 1s ease-in-out forwards;
}
.projectDetail > * {
  opacity: 0;
  animation: projectDetailFadeIn 1s 500ms ease-in-out forwards;
}

.projectDetail .logoDiv {
  padding-top: 10px;
  display: flex;
  justify-content: center;
  height: 440px;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.9));
}

.projectDetail .twitterDiv {
  width: 99%;
  height: 20px;
  display: flex;
  flex-direction: row;
  justify-content: right;
  align-items: center;
  column-gap: 5px;
}

.projectDetail .locationDiv:hover {
  cursor: pointer;
}

.projectDetail .locationDiv > img {
  content: url("assets/images/location-icon.PNG");
  width: 20px;
  height: 20px;
}

.projectDetail .twitterLink {
  width: auto;
  height: 100%;
}
.projectDetail .twitterLink > img {
  content: url("assets/images/twitter-icon.PNG");
  width: 100%;
  height: 100%;
}

.projectDetail .dateNPlaceDiv {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  column-gap: 20px;
}
.projectDetail .dateNPlaceDiv > h2 {
  border-right: 4px solid black;
  padding: 5px 20px;
}

.projectDetail .menuDiv {
  margin-bottom: 0rem;
}
.projectDetail .menuDiv > h1, .projectDetail .giftDiv > h1 {
  margin-bottom: 0.5rem;
}
.projectDetail .menuDiv dt, .projectDetail .giftDiv dt {
  display:block;
  float:left;
  width:50%;
  text-align: right;
}
.projectDetail .menuDiv dt:after, .projectDetail .giftDiv dt:after{
  content:':';
  font-style: normal;
  padding: 5px;
}
.projectDetail .menuDiv dd, .projectDetail .giftDiv dd {
  display:block;
  text-align: left;
  margin: 2px 0px;
}
.projectDetail .staticPicDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.projectDetail .staticPicDiv .standeeDiv{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.projectDetail .giftDiv {
  margin-bottom: 3rem;
  text-align: center;
}


.mat-mdc-dialog-surface, .mdc-dialog__surface {
  background-color: rgba(0, 0, 0, 0.8) !important;
}

.mat-mdc-tooltip .mdc-tooltip__surface {
  background-color: rgba(0, 0, 0, 0.5) !important;
  font-family: Mitr, san-serif !important;
  padding: 0.5rem 1rem;
}

@keyframes ray {
  0% {
    width: 1%;
    height: 1vh;
    left: 50vw;
  }
  50% {
    width: 1%;
    height: 65vh;
    left: 50vw;
  }
  100% {
    width: 80%;
    height: 70vh;
    left: 10vw;
  }
}

@keyframes projectDetailFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


/* -- scrollbar ----------------------------------------*/

/* width */
::-webkit-scrollbar {
  width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.3);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.4);;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.4);
}

@media only screen  and (orientation: portrait) {
  .appBlock {
    display: block;
  }
}

